import React from "react";

class Menu extends  React.Component{
    render(){
        const {isSelected,list} = this.props;
        return(
            <ul className={"list"}>
                {list.map((el)=>{
                    let a = el===isSelected?"list_item list_active":"list_item"
                    return(<li key={el} className={a}>{el}</li>)
                })}
                {/*<li className={isSelected==="Music"?"list_item list_active":"list_item"}>Music</li>*/}
                {/*<li className={isSelected==="Games"?"list_item list_active":"list_item"}>Games</li>*/}
                {/*<li className={isSelected==="Settings"?"list_item list_active":"list_item"}>Settings</li>*/}
            </ul>

        )
    }
}

export default Menu;