

import React from 'react';
import ZingTouch from 'zingtouch';


class Buttons extends React.Component {
    isRotated = this.props.isRotated;
    componentDidMount(){
        let myElement = document.getElementById('buttons');
        let myRegion = new ZingTouch.Region(myElement);
        let chainableObject = myRegion.bind(myElement);

        chainableObject
            .rotate((e) =>{
                this.isRotated(e.detail);
            },false)

    }
    render() {
        const {isSelect,isMenu,isPlay,isElseButton} = this.props;
        return (
            <div id={"buttons"} className="buttons-container">
                <button className="center-circle" onClick={isSelect}>
                    <h2>Select</h2>
                </button>

                <button onClick={isMenu} className="menu-button">
                    <i className="fas fa-bars"></i>
                </button>
                <button onClick={isElseButton} className="left-button">
                    <i className="fas fa-backward"></i>
                </button>
                <button onClick={isElseButton} className="right-button">
                    <i className="fas fa-forward"></i>
                </button>
                <button onClick={(e)=>{isPlay(e)}} className="play-pause">
                    <i className="fas fa-play"></i>
                </button>
            </div>
        );
    }
}

export default Buttons;