import React from "react";
import Buttons from "./Buttons";
import Menu from "./Menu";
import $ from 'jquery';

class Pod extends React.Component{
    angleRotated = 0;
    constructor(){
        super();
        this.state={
            isScreen:"Menu",
            isSelected:"Music",
            list:["Music","Games","Settings"],
        }
    }
    handleSelect = () =>{
        this.setState((prevState)=>{
            let {isSelected} = prevState;
            if(isSelected===""){
                return prevState
            }
            if(isSelected==="Music"){
                return({
                    isScreen:isSelected,
                    isSelected:"All Songs",
                    list:["All Songs","Artists","PlayList"]
                })
            }
            else{
                return({
                    isScreen:isSelected,
                    isSelected:"",
                    list:[]
                })
            }
        })
    }

    handleMenu = () =>{
        this.setState({
            isScreen:"Menu",
            isSelected:"Music",
            list : ["Music","Games","Settings"]
        })
    }

    handleRotate = (data)=>{
        if((data.distanceFromLast>0 && this.angleRotated>=0)||(data.distanceFromLast<0 && this.angleRotated<=0)){
            this.angleRotated+= data.distanceFromLast
        }
        else{
            this.angleRotated = data.distanceFromLast
        }
        if(Math.abs(this.angleRotated)>25){
                this.setState((prevState) =>{
                    let isSelected = prevState.isSelected;
                    let index = prevState.list.indexOf(isSelected);
                    if(this.angleRotated<0) {
                        if (index === 0) index = 3;
                        this.angleRotated =0;
                        return {isSelected: prevState.list[index - 1]}
                    }else{
                        if(index===2) index=-1;
                        this.angleRotated =0;
                        return {isSelected:prevState.list[index+1]}
                    }
                })

        }

    }
    playing = true
    handlePlay = () =>{
        let iframe = document.getElementsByTagName("iframe")[0];
        if(!iframe){
            return;
        }
        let id = iframe.getAttribute('id')
        iframe.setAttribute('src', '');

        if(this.playing) {
            iframe.setAttribute('src', id);
        }
        else{
            iframe.setAttribute('src', id+'?autoplay=1');
        }
        this.playing=!this.playing
    }
    handleButton = () =>{
        let iframe = document.getElementsByTagName("iframe")[0];
        if(!iframe){
            return;
        }
        let id = iframe.getAttribute('id')
        iframe.setAttribute('src', '');
        iframe.setAttribute('src', id+'?autoplay=1');
    }
    render(){
        return (
            <div className={"d-flex flex-column justify-content-center m-auto bg-dark text-danger p-2" } id={"pod-outline"}>
                <div className={"m-auto"} id={"screen"}>
                    {this.state.isScreen === "Menu" && <Menu isSelected={this.state.isSelected} list={this.state.list}/>}
                    {this.state.isScreen === "Music" && <Menu isSelected={this.state.isSelected} list={this.state.list}/>}
                    {this.state.isScreen=== "Games" && <img alt={"Games"} className={"screen_img "} src={"https://cdn-icons-png.flaticon.com/512/686/686589.png"}/>}
                    {this.state.isScreen=== "Settings" && <img alt={"Settings"} className={"screen_img "} src={"https://cdn-icons.flaticon.com/png/512/563/premium/563541.png?token=exp=1657014417~hmac=d3a9e2cf301bd438cb1eb8f29b31d98c"}/>}
                    {this.state.isScreen==="All Songs" &&
                        <iframe width="250" height="250" id="https://www.youtube.com/embed/tscMSXk_jaQ" src="https://www.youtube.com/embed/tscMSXk_jaQ?autoplay=1"
                                title="YouTube video player"
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen></iframe>}
                    {this.state.isScreen==="Artists" &&
                        <iframe width="250" height="250" id="https://www.youtube.com/embed/5ado75KpV9w" src="https://www.youtube.com/embed/5ado75KpV9w?autoplay=1"
                                title="YouTube video player"
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen></iframe>}
                    {this.state.isScreen==="PlayList" &&
                        <iframe width="250" height="250" id="https://www.youtube.com/embed/PKonqhZ5PsA" src="https://www.youtube.com/embed/PKonqhZ5PsA?autoplay=1"
                                title="YouTube video player"
                                allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                                allowFullScreen></iframe>}
                </div>
                <Buttons
                    isElseButton={this.handleButton}
                    isPlay={this.handlePlay}
                    isSelect={this.handleSelect}
                    isMenu={this.handleMenu}
                    isRotated={this.handleRotate}/>
            </div>
        )
    }

}


export default Pod;